/*************************************************************/
/**************************General****************************/
/*************************************************************/

@import url("https://fonts.googleapis.com/css2?family=Itim&display=swap");
html {
  font-size: 62.5%;
}

body {
  margin: 0;
  height: 100vh;
  font-size: 1.6rem;
  font-family: "Itim", cursive;

  background-color: #ffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
}
.bigContainer {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 10rem 1fr 4rem;
  height: 100%;
}
ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

/*************************************************************/
/**************************Header*****************************/
/*************************************************************/
header {
  background-color: #282c34;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}
/*************************************************************/
/**************************Main*******************************/
/*************************************************************/
main {
  display: grid;
  grid-template-columns: 10rem 5fr;
}
main section.leftAside {
  background-color: #282c34;
}
.sideBarItem {
  height: 9rem;
  text-align: center;
  font-size: 1.2rem;
  border-bottom: white solid 0.1rem;
  color: white;
}
.sideBarItem:hover {
  cursor: pointer;
  background-color: #67686b;
}
.sideBarItem img {
  width: auto;
  height: 5rem;
  margin-bottom: 1rem;
}
.sideBarItem h1 {
  margin: 0 0 0.5rem 0;
  padding: 0;
}
/*************************************************************/
/**************************Screens****************************/
/*************************************************************/
.screens {
  text-align: center;
}

/*************************************************************/
/**************************Logo*******************************/
/*************************************************************/
.App-logo {
  height: 8rem;
  pointer-events: none;
  position: absolute;

  top: 0;
}
.App-logo-static {
  height: 8rem;
  position: absolute;

  top: 0;
}
.logo-container {
  position: relative;
  height: 8rem;
  width: 8rem;
}
.logo-container-big {
  position: relative;
  width: 100%;
  height: 100%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 10s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/*************************************************************/
/**************************Footer*****************************/
/*************************************************************/
footer {
  background-color: #282c34;
  align-items: center;
  justify-content: space-around;
  color: white;
  display: flex;
}
footer h3 {
  margin: 0;
}

/*************************************************************/
/**************************Responsive*************************/
/*************************************************************/
@media (max-width: 700px) {
  main {
    grid-template-columns: 1fr 3fr;
  }
}
